import { Module } from "vuex";
import { EasemobChat } from "easemob-websdk/Easemob-chat";
import { MiniCore } from "easemob-websdk/publish/websdk/miniCore/miniCore";

interface ChatMap {
  [key: string]: any;
}
export interface IMState {
  connect: MiniCore;
  chat: ChatMap;
  conversationList: Array<any>;
  pin: ChatMap;
  pinMessageIdsMap: Record<string, Array<string>>;
}

const im: Module<IMState, IMState> = {
  namespaced: true,
  state: {
    connect: {} as MiniCore,
    chat: {},
    conversationList: [],
    pin: {},
    pinMessageIdsMap: {}
  },
  mutations: {
    setConnect(state, conn) {
      state.connect = conn;
    },
    pushMessage(state, { fromId, message }) {
      if (Object.prototype.hasOwnProperty.call(state.chat, fromId)) {
        state.chat[fromId].messageList.push(message);
      } else {
        state.chat[fromId] = {
          messageList: [message]
        };
      }
    },
    deleteMessage(state, { fromId, id }) {
      if (state.chat[fromId]) {
        const idx = state.chat[fromId].messageList.findIndex(
          (item: EasemobChat.MessageBody) => {
            return item.id === id;
          }
        );
        if (idx > -1) {
          state.chat[fromId].messageList.splice(idx, 1);
        }
      }
    },
    updateMessage(
      state,
      params: { fromId: string; modifiedMsg: EasemobChat.ModifiedMsg }
    ) {
      const { fromId, modifiedMsg } = params;
      if (state.chat[fromId]) {
        const idx = state.chat[fromId].messageList.findIndex(
          (item: EasemobChat.MessageBody) => {
            return item.id === modifiedMsg.id;
          }
        );
        if (idx > -1) {
          if (modifiedMsg.type === 'txt') {
            state.chat[fromId].messageList[idx].msg = modifiedMsg.msg;
          } else if (modifiedMsg.type === "custom") {
            state.chat[fromId].messageList[idx].customExts =
              modifiedMsg.customExts;
            state.chat[fromId].messageList[idx].customEvent =
              modifiedMsg.customEvent;
          }

          state.chat[fromId].messageList[idx].modifiedInfo =
            modifiedMsg.modifiedInfo;
          if (modifiedMsg.ext) {
            state.chat[fromId].messageList[idx].ext = modifiedMsg.ext;
          }
        }
      }
    },
    clearPinnedMessage(state, { fromId }) {
      if (state.pin[fromId]) {
        state.pin[fromId] = {
          messageList: []
        };
      }
    },
    pushPinedMessage(state, { fromId, message }) {
      if (Object.prototype.hasOwnProperty.call(state.pin, fromId)) {
        state.pin[fromId].messageList.push(message);
      } else {
        state.pin[fromId] = {
          messageList: [message]
        };
      }
      state.pinMessageIdsMap[fromId] = state.pin?.[fromId]?.messageList.map(
        (item: any) => {
          return item.message.id;
        }
      );
    },
    unshiftPinedMessage(state, { fromId, message }) {
      if (Object.prototype.hasOwnProperty.call(state.pin, fromId)) {
        state.pin[fromId].messageList.unshift(message);
      } else {
        state.pin[fromId] = {
          messageList: [message]
        };
      }
      state.pinMessageIdsMap[fromId] = state.pin?.[fromId]?.messageList.map(
        (item: any) => {
          return item.message.id;
        }
      );
    },
    deletePinedMessage(state, { fromId, id }) {
      if (state.pin[fromId]) {
        const idx = state.pin[fromId].messageList.findIndex(
          (item: EasemobChat.MessageBody) => {
            return item.id === id;
          }
        );
        if (idx > -1) {
          state.pin[fromId].messageList.splice(idx, 1);
        }
      }
      state.pinMessageIdsMap[fromId] = state.pin?.[fromId]?.messageList.map(
        (item: any) => {
          return item.message.id;
        }
      );
    },
    updatePinedMessage(state, { fromId, modifiedMsg }) {
      if (state.pin[fromId]) {
        const idx = state.pin[fromId].messageList.findIndex(
          (item: EasemobChat.MessageBody) => {
            return item.id === modifiedMsg.id;
          }
        );
        if (idx > -1) {
          state.pin[fromId].messageList[idx].msg = modifiedMsg.msg;
          state.pin[fromId].messageList[idx].modifiedInfo =
            modifiedMsg.modifiedInfo;
        }
      }
      state.pinMessageIdsMap[fromId] = state.pin?.[fromId]?.messageList.map(
        (item: any) => {
          return item.message.id;
        }
      );
    },
    setConversationList(state, { list }) {
      state.conversationList = list;
    },
    clearConversationUnreadCount(state, { conversationId, conversationType }) {
      const { connect, conversationList } = state;
      connect.localCache
        .clearConversationUnreadCount({ conversationId, conversationType })
        .then(() => {
          const idx = conversationList.findIndex((item) => {
            return (
              item.conversationId === conversationId &&
              item.conversationType === conversationType
            );
          });
          conversationList[idx].unReadCount = 0;
          state.conversationList = conversationList;
        });
    },
    setConversationCustomField(
      state,
      { conversationId, conversationType, customField }
    ) {
      const { conversationList, connect } = state;
      const opt = {
        conversationId,
        conversationType,
        customField: {
          customkey: customField
        }
      };
      connect.localCache
        .setLocalConversationCustomField(opt)
        .then((res: any) => {
          const idx = conversationList.findIndex((item) => {
            return (
              item.conversationId === conversationId &&
              item.conversationType === conversationType
            );
          });
          conversationList[idx].customField = customField;
          state.conversationList = conversationList;
        });
    },
    removeLocalConversation(state, { conversationId, conversationType }) {
      const { conversationList, connect } = state;
      connect.localCache
        .removeLocalConversation({ conversationId, conversationType })
        .then(() => {
          const idx = conversationList.findIndex((item) => {
            return (
              item.conversationId === conversationId &&
              item.conversationType === conversationType
            );
          });
          if (idx === -1) return;
          conversationList.splice(idx, 1);
          state.conversationList = conversationList;
        });
    }
  }
};

export default im;
